@import "normalize.scss";
@import "variables.scss";
.wrapper {
	margin: 0 auto;
}
.brands {
	border-top: 1px solid $hl-color;
	position: relative;
	margin-bottom: 40px;
	.brands__title {
		font-family: TT Lakes;
		font-size: 1.6rem;
		line-height: 2.4rem;
		letter-spacing: -0.20000000298023224px;
		text-align: left;
		padding: 16px 0;
		text-transform: uppercase;
		background: linear-gradient(270deg, #f8f8f8 0%, #ffffff 98.61%);
		margin-bottom: 16px;
		h1 {
			padding: 0 16px;
		}
	}
	.brands__list {
		padding: 0 16px;
		min-width: 320px;
		.brands__item {
			display: flex;
			height: 72px;
			width: 240px;
			border: 1px solid $border-color;
			border-radius: 6px;
			justify-content: space-between;
			align-items: center;
			background-color: $icon-color;
			> img {
				display: block;
				object-fit: scale-down;
				margin-left: 16px;
			}
			.brands__button {
				@extend %button;
				margin-right: 16px;
			}
		}
	}
	.swiper-pagination-bullets {
		bottom: -30px;
	}
}

.brands-swiper {
	margin-bottom: 30px;
}

.brands__light-shadow {
	width: 24px;
	height: 81px;
	background: linear-gradient(270deg, #f8f8f8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
	position: absolute;
	right: 0;
	top: 65px;
	z-index: 100;
}

.brands .swiper-pagination-bullet {
	width: 12px;
	height: 12px;
	background-color: #b5b6bc;
}

.brands__readMore {
	display: none;
	font-family: TT Lakes;
	font-size: 16px;
	font-weight: 500;
	line-height: 24px;
	letter-spacing: -0.20000000298023224px;
	text-align: left;
	margin-top: 24px;
	margin-left: 63px;
	position: relative;
	&::before {
		content: attr(data-icon);
		position: absolute;
		background-image: url("../img/icons/button_down.svg");
		left: -17px;
		top: 20%;
		width: 7.5px;
		height: 10.29px;
		transform: none;
		transition: all;
		transition-duration: 1000ms;
	}
}

.down.brands__readMore::before {
	transform: rotate(180deg);
}
