@import "variables.scss";

@media (min-width: 1366px) {
	.header__body {
		display: none !important;
	}
	.menu {
		left: 0px;
		box-shadow: none;
		.menu__burger {
			display: none;
		}
	}
	.main {
		margin-left: 320px;
		margin-top: 0px;
		.main__container {
			margin-top: 42px;
		}
		.main__title {
			padding-top: 0;
		}
	}

	.sliderMenu {
		flex-wrap: wrap;
		.sliderMenu__button {
			padding: 8px 22px 7px 23px;
		}
	}

	.title {
		padding: 0;
		.title__repair {
			display: block;
			&::after {
				display: block;
			}
		}
		.title__checkstatus {
			display: block;
		}
	}
	.brands {
		margin-left: 320px;
		.brands__title {
			padding: 16px 32px 16px;
		}
		.brands__list {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
			column-gap: 32px;
			row-gap: 16px;
			padding: 0 32px 0 32px;
			margin-top: 24px;
			.brands__item {
				width: 22.5%;
			}
		}
		.brands__readMore {
			margin-left: 63px;
		}
	}
}
