@import "variables.scss";

@media (min-width: 768px) {
	.header__body {
		padding: 19px 24px;
	}
	.header__logo {
		margin-left: 20px;
		margin-right: auto;
		&::after {
			content: "";
			position: absolute;
			width: 2px;
			height: 32px;
			background-color: #eaeaea;
			left: -20px;
			top: 25%;
		}
	}
	.header__call {
		display: block;
		position: relative;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $icon-bg;
		align-self: center;
		img {
			position: absolute;
			top: 25%;
			left: 25%;
		}
	}

	.header__message {
		display: block;
		position: relative;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $icon-bg;
		align-self: center;
		img {
			position: absolute;
			top: 25%;
			left: 25%;
			width: 20px;
			height: 20px;
		}
	}

	.header__profile {
		display: block;
		position: relative;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $icon-bg;
		align-self: center;
		margin-right: 20px;
		img {
			position: absolute;
			top: 25%;
			left: 25%;
			width: 20px;
			height: 20px;
		}
	}
	.main {
		.main__container {
			margin: 24px 24px 0;
		}
		.main__title::before {
			left: -24px;
		}
	}
	.title::before {
		left: -24px;
	}
	.block1{
	column-gap:48px;
	}
	.block1__content {
		flex-basis: 45%;
	}

	.block1__image {
		flex-basis: 48%;
	}
	.brands__title {
		padding: 16px 24px;
	}
	.swiper-pagination {
		display: none;
	}
	.brands__readMore {
		display: block;
	}
	.brands {
		margin-bottom: 32px;
		.brands__title {
			background: linear-gradient(90deg, #f8f8f8 1.39%, #ffffff 100%);
			h1 {
				padding-left: 24px;
			}
		}
		.brands__list {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
			column-gap: 24px;
			row-gap: 16px;
			padding: 0 24px 0 24px;
			.brands__item {
				width: 31%;
			}
		}
		.brands__list.closed {
			height: 160px;
			overflow: hidden;
		}
	}

	.swiper-wrapper {
		box-sizing: border-box;
	}
	.brands__light-shadow {
		display: none;
	}
}
