@import "normalize.scss";
@import "variables.scss";

.tech-types {
  margin: 0 auto;
  border-top: 1px solid $hl-color;
  position: relative;
  .tech-types__title {
    font-family: TT Lakes;
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: -0.20000000298023224px;
    text-align: left;
    padding: 16px 0;
    text-transform: uppercase;
    background: linear-gradient(270deg, #f8f8f8 0%, #ffffff 98.61%);
    margin-bottom: 40px;
  }
  h1 {
    padding: 0 16px;
  }
  .tech-types__list {
    padding: 0 16px;
    min-width: 320px;
    .tech-types__item {
      width: 240px;
      height: 160px;
      background-color: $block-bg;
      border: 1px solid $border-color;
      border-radius: 6px;
      position: relative;
      display: flex;
      justify-content: space-between;
      column-gap: 32px;
      padding: 16px;
      span {
        display: block;
        font-family: 'TT Lakes';
font-size: $titles-fz;
line-height: 24px;
/* identical to box height, or 150% */

letter-spacing: -0.2px;
      }
    }
  }
  .tech-types__button {
    @extend %button;
    align-self: flex-end;
  }
}
